import { z } from 'zod';
import * as models from './models';
import { SchemaRegistryError, SchemaRegistryErrorCode, ILogger } from './common';

export const BillSchema = z.object({
  billId: z.string(),
  billNumber: z.string(),
  tradePartnerBillNumber: z.string(),
  tradePartnerId: z.string(),
  tradePartnerName: z.string().optional(),
  billType: z.string(),
  billSource: z.string(),
  billDate: z.string().regex(/^\d{4}-\d{2}-\d{2}$/, { message: "Must be a valid ISO 8601 date (YYYY-MM-DD)" }),
  billStatus: z.string(),
  totalAmountInCents: z.number().int(),
  isTradePartnerCredit: z.boolean().optional(),
  paymentTerms: z.string().optional(),
  dueDate: z.string().regex(/^\d{4}-\d{2}-\d{2}$/, { message: "Must be a valid ISO 8601 date (YYYY-MM-DD)" }).optional(),
  amountPaidInCents: z.number().int().optional(),
  paidDate: z.string().regex(/^\d{4}-\d{2}-\d{2}$/, { message: "Must be a valid ISO 8601 date (YYYY-MM-DD)" }).nullable().optional(),
  externalSystemBillId: z.string().nullable().optional(),
  reversalBillId: z.string().nullable().optional(),
  postedDate: z.string().regex(/^\d{4}-\d{2}-\d{2}$/, { message: "Must be a valid ISO 8601 date (YYYY-MM-DD)" }).nullable().optional(),
});

export const ProjectSchema = z.object({
  projectId: z.string(),
  projectName: z.string(),
  lotType: z.string(),
  projectStatus: z.string(),
  warrantyStartDate: z.string().regex(/^\d{4}-\d{2}-\d{2}$/, { message: "Must be a valid ISO 8601 date (YYYY-MM-DD)" }).nullable().optional(),
  verticalCompletionDate: z.string().regex(/^\d{4}-\d{2}-\d{2}$/, { message: "Must be a valid ISO 8601 date (YYYY-MM-DD)" }).nullable().optional(),
});

export const LineItemSchema = z.object({
  lineId: z.string(),
  amountInCents: z.number().int(),
  paidInCents: z.number().int().optional(),
  costCodeId: z.string(),
  costCodeNumber: z.string(),
  costClassification: z.string(),
  costDivision: z.string().nullable().optional(),
  costCodeVersion: z.number().int().optional(),
  sourceId: z.string().optional(),
  description: z.string().optional(),
});

export const ApprovalSchema = z.object({
  approvalStatus: z.string(),
  approvalDate: z.string().datetime({ message: "Must be a valid ISO 8601 UTC datetime (YYYY-MM-DDThh:mm:ss.sssZ)" }).nullable().optional(),
  rejectionDate: z.string().datetime({ message: "Must be a valid ISO 8601 UTC datetime (YYYY-MM-DDThh:mm:ss.sssZ)" }).nullable().optional(),
  rejectionReason: z.string().nullable().optional(),
});

export const MetadataSchema = z.object({
  idempotencyKey: z.string().optional(),
  correlationId: z.string().optional(),
  eventTimeStamp: z.string().datetime({ message: "Must be a valid ISO 8601 UTC datetime (YYYY-MM-DDThh:mm:ss.sssZ)" }),
  schemaVersion: z.string(),
});

export const BillEventSchema = z.object({
  bill: BillSchema,
  project: ProjectSchema,
  lineItems: z.array(LineItemSchema),
  approval: ApprovalSchema,
  eventMetadata: MetadataSchema,
});

